  import { quintOut } from "svelte/easing";
  import * as animateScroll from "svelte-scrollto";

  let show: boolean = false;
  let scroll: number;

  $: {
    if (scroll > 300) {
      show = true;
    } else {
      show = false;
    }
  }

  const up = () => {
    animateScroll.scrollToTop();
  };
</script>

